.content {
  font-family: TT Lakes, sans-serif;
  background-color: #F8F8F8;
  color: #1B1C21; }
  .content .container {
    display: flex;
    flex-direction: column;
    max-width: 760px; }
    @media (min-width: 768px) {
      .content .container {
        max-width: 1110px; } }
    @media (min-width: 1120px) {
      .content .container {
        width: 1040px; } }
  .content .content__container {
    z-index: 0;
    position: relative;
    padding: 0 16px;
    margin: 0 auto; }
    .content .content__container .site-body {
      display: flex; }
    @media (min-width: 768px) {
      .content .content__container {
        max-width: 1110px;
        margin: 0 auto; } }

.modal-tel_closed {
  display: none; }

.close-btn {
  position: absolute;
  top: 24px;
  width: 40px;
  height: 40px;
  background: url(./static/pink-close-btn.svg); }
  @media (min-width: 768px) {
    .close-btn {
      left: -65px; } }

.modal-tel__container {
  width: 288px;
  height: 1024px;
  padding: 104px 16px; }
  @media (min-width: 768px) {
    .modal-tel__container .modal-tel .title__highlight::after {
      margin-left: 0px; } }
  .modal-tel__container .modal-tel .modal__input {
    font-size: 16px;
    padding: 12px 70px 12px 16px;
    margin-bottom: 16px;
    border: 1px solid #EAEAEA;
    border-radius: 5px; }
    @media (min-width: 768px) {
