    width: 270px;
    color: #7E7E82;
    font-size: 14px; }
    @media (min-width: 768px) {
      .modal-tel__container .modal-tel__info {
        width: 380px; } }
  .modal-tel__container .modal-tel .submit-btn {
    position: absolute;
    top: 300px;
    right: 0px;
    bottom: 0px;
    content: "";
