.content {
  font-family: TT Lakes, sans-serif;
  background-color: #F8F8F8;
  color: #1B1C21; }
  .content .container {
    display: flex;
    flex-direction: column;
    max-width: 760px; }
    @media (min-width: 768px) {
      .content .container {
        max-width: 1110px; } }
    @media (min-width: 1120px) {
      .content .container {
        width: 1040px; } }
  .content .content__container {
    z-index: 0;
    position: relative;
    padding: 0 16px;
    margin: 0 auto; }
    .content .content__container .site-body {
      display: flex; }
    @media (min-width: 768px) {
      .content .content__container {
        max-width: 1110px;
        margin: 0 auto; } }

.header-inner {
  display: flex;
  background-color: white;
  height: 88px; }
  @media (min-width: 768px) {
    .header-inner {
      background-color: white;
      max-width: 1110px; } }
  @media (min-width: 1120px) {
    .header-inner {
      display: none; } }

.header-inner__container {
  margin: 0 -16px;
  padding: 0 16px;
  width: 100vw; }
  .header-inner__container .header-inner__burger {
    width: 40px;
    height: 40px;
    margin: 24px auto;
    background: url(./static/burger.svg) no-repeat; }
    @media (min-width: 768px) {
      .header-inner__container .header-inner__burger {
        position: relative; }
        .header-inner__container .header-inner__burger::after {
          content: " ";
          position: absolute;
          background: #EAEAEA;
          width: 2px;
          height: 32px;
          left: 10vw;
          top: 1vh; } }
  .header-inner__container .header-inner__logo {
    display: inline-block;
    position: relative;
    width: 97px;
    height: 51px;
    background: url(./static/cps-logo.svg) no-repeat;
    margin: 18px auto; }
    @media (min-width: 768px) {
      .header-inner__container .header-inner__logo::after {
        display: none; } }
  .header-inner__container .header-inner__tools {
    width: 40px;
    height: 40px;
    background: url(./static/tools.svg) no-repeat;
    margin: 24px auto; }
  .header-inner__container .header-inner__search {
    width: 40px;
    height: 40px;
    background: url(./static/search.svg) no-repeat;
    margin: 24px auto; }
  .header-inner__container .header-inner__text {
    display: none; }
    @media (min-width: 1120px) {
      .header-inner__container .header-inner__text {
        display: block;
        position: relative;
        width: 160px;
        padding-left: 24px;
        background-color: #F8F8F8;
        color: black;
        height: 24px;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: -0.2px; } }
  .header-inner__container .header-inner__tel {
    display: none; }
    @media (min-width: 768px) {
      .header-inner__container .header-inner__tel {
        display: block;
        width: 40px;
        height: 40px;
        margin: auto 20px;
        background: url(./static/tel.svg) no-repeat; } }
  .header-inner__container .header-inner__chat {
    display: none; }
    @media (min-width: 768px) {
      .header-inner__container .header-inner__chat {
        display: block;
        width: 40px;
        height: 40px;
        margin: auto 20px;
        background: url(./static/chat.svg) no-repeat; } }
  .header-inner__container .header-inner__profile {
    display: none; }
    @media (min-width: 768px) {
      .header-inner__container .header-inner__profile {
        display: block;
        width: 40px;
        height: 40px;
        margin: auto 20px;
        background: url(./static/profile.svg) no-repeat; } }
