.content {
  font-family: TT Lakes, sans-serif;
  background-color: #F8F8F8;
  color: #1B1C21; }
  .content .container {
    display: flex;
    flex-direction: column;
    max-width: 760px; }
    @media (min-width: 768px) {
      .content .container {
        max-width: 1110px; } }
    @media (min-width: 1120px) {
      .content .container {
        width: 1040px; } }
  .content .content__container {
    z-index: 0;
    position: relative;
    padding: 0 16px;
    margin: 0 auto; }
    .content .content__container .site-body {
      display: flex; }
    @media (min-width: 768px) {
      .content .content__container {
        max-width: 1110px;
        margin: 0 auto; } }

.repair__title-box {
  margin: 24px 0px 16px -16px;
  padding: 16px;
  background-image: linear-gradient(to right, #F8F8F8, white); }

.repair-title {
  display: inline-block;
  width: 90vw;
  color: #7E7E82;
  line-height: 24px;
  letter-spacing: -0.2px;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase; }

.swiper-container {
  position: relative;
  width: 240px;
  height: 72px; }

.swiper-slide {
  background-color: white;
  border: 1px solid #EAEAEA;
  border-radius: 5px;
  position: relative; }

.logo-lenovo {
  padding: 20px 116px 20px 16px; }

.logo-samsung {
  padding: 17px 108px 17px 8px; }

.logo-apple {
  padding: 12px 108px 12px 12px; }

.logo-view {
  padding: 26px 98px 26px 16px; }

.logo-bosch {
  padding: 22px 96px 22px 16px; }

.logo-hp {
  padding: 10px 178px 10px 10px; }

.logo-acer {
  padding: 24px 130px 24px 16px; }

.logo-sony {
  padding: 23px 98px 23px 14px; }

.repair__swiper-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  content: "";
  width: 40px;
  height: 40px;
  background: url(./static/pink-btn.svg); }

.swiper-horizontal > .swiper-pagination-bullets {
  top: 88px;
  left: 20vw; }

.swiper-pagination.swiper-pagination-horizontal {
  z-index: 1; }

.swiper-pagination-bullet {
  width: 12px;
  height: 12px; }

.swiper-pagination-bullet-active {
  background-color: #7E7E82; }

.show-all-btn {
  display: none; }

/* ---------------------768px--------------------- */
/* ---------------------768px--------------------- */
@media (min-width: 768px) {
  .swiper-pagination {
    display: none; }
  .repair {
    height: 240px; }
  .repair_bigger {
    height: 420px; }
  .swiper-container {
    display: flex; }
  .swiper-wrapper {
    display: grid;
    grid-template-columns: 30vw 30vw 30vw;
    gap: 15px; }
  .swiper-slide {
    margin-right: 0; }
  .show-all-btn {
    display: block;
    position: relative;
    padding: 0;
    background-color: #F8F8F8;
    color: black;
    width: 140px;
    height: 40px; }
    .show-all-btn::before {
      position: absolute;
      display: inline-block;
      content: "";
      width: 30px;
      height: 30px;
      top: 5px;
      bottom: 5px;
      right: 110px;
      background: url(./static/blue-arrows_down.svg) 0px 0px no-repeat; }
  .show-less-btn {
    display: block;
    position: relative;
    padding: 0;
    background-color: #F8F8F8;
    color: black;
    width: 140px;
    height: 40px; }
    .show-less-btn::before {
      position: absolute;
      display: inline-block;
      content: "";
      width: 30px;
      height: 30px;
      top: 0px;
      bottom: 5px;
      right: 110px;
      background: url(./static/blue-arrows_down.svg) 0px 0px no-repeat;
      transform: rotate(180deg); }
  .swiper-slide {
    display: block; }
  .repair__swiper-slide_none {
    display: none; } }

