.content {
  font-family: TT Lakes, sans-serif;
  background-color: #F8F8F8;
  color: #1B1C21; }
  .content .container {
    display: flex;
    flex-direction: column;
    max-width: 760px; }
    @media (min-width: 768px) {
      .content .container {
        max-width: 1110px; } }
    @media (min-width: 1120px) {
      .content .container {
        width: 1040px; } }
  .content .content__container {
    z-index: 0;
    position: relative;
    padding: 0 16px;
    margin: 0 auto; }
    .content .content__container .site-body {
      display: flex; }
    @media (min-width: 768px) {
      .content .content__container {
        max-width: 1110px;
        margin: 0 auto; } }

.heading-box {
  display: flex; }
  .heading-box .title {
    font-family: TT Lakes;
    font-size: 32px;
    font-weight: 700;
    line-height: 40px;
    letter-spacing: -0.6px; }
    @media (min-width: 1120px) {
      .heading-box .title {
        padding-top: 10px;
        padding-right: 250px; } }
  .heading-box .title__highlight {
    position: relative; }
    .heading-box .title__highlight::after {
      position: absolute;
      content: "";
      width: 4px;
      height: 40px;
      background: #41F6D7;
      left: -16px;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px; }
  .heading-box .heading-box__title {
    margin-top: 24px; }
    @media (min-width: 1120px) {
      .heading-box .heading-box__title {
        margin-top: 0px; } }

.heading-box__search {
  display: none; }
  @media (min-width: 1120px) {
    .heading-box__search {
      padding-top: 10px;
      margin-top: 10px;
      position: relative;
      display: flex;
      width: auto;
      height: 52px;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      background-color: #F8F8F8; }
      .heading-box__search::after {
        position: absolute;
        content: " ";
        width: 40px;
        height: 40px;
        background: url(./static/tools.svg) no-repeat;
        top: 4px;
        left: 130px; } }

.heading-box__tools {
  display: none; }
  @media (min-width: 1120px) {
    .heading-box__tools {
      padding-top: 10px;
      margin-top: 10px;
      margin-right: 50px;
      position: relative;
      display: flex;
      width: auto;
      height: 52px;
      font-size: 16px;
      line-height: 24px;
      letter-spacing: -0.2px;
      background-color: #F8F8F8; }
      .heading-box__tools::after {
        position: absolute;
        content: " ";
        width: 40px;
        height: 40px;
        background: url(./static/search.svg) no-repeat;
        top: 4px;
        left: 130px; } }

.service-list {
  display: flex;
  overflow: hidden;
  overflow-x: scroll;
  max-width: 700px;
  height: 64px;
  padding-top: 24px;
  scrollbar-width: none; }
  .service-list .service-list__item {
    display: flex;
    flex: none;
    justify-content: center;
    align-items: center;
    color: #7E7E82;
    width: auto;
    height: 40px;
    padding: 7px 21px;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px; }
  .service-list .service-list__item--active {
    flex: none;
    border: 0.5px solid #B8FFEC;
    border-radius: 8px; }
  @media (min-width: 768px) {
    .service-list {
      max-width: 1110px; } }
  @media (min-width: 1120px) {
    .service-list {
      max-width: 1063px;
      height: 82px;
      overflow-x: visible;
      flex-wrap: wrap;
      padding: 0; } }

.about-us {
  display: flex;
  flex-direction: column; }
  .about-us .about-us__paragraph {
    display: block;
    max-width: 90vw;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.2px;
    padding-top: 24px; }
    @media (min-width: 768px) {
      .about-us .about-us__paragraph {
        text-align: left;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.2px;
        max-width: 319px;
        height: 160px;
        margin-top: 0px;
        margin-right: 48px; } }
    @media (min-width: 1120px) {
      .about-us .about-us__paragraph {
        max-width: 500px;
        height: 160px;
        margin: 0; } }
  @media (min-width: 1120px) {
    .about-us .about-us__paragraph--first {
      margin-bottom: 30px; } }
  .about-us .about-us__paragraph_second {
    display: none; }
    @media (min-width: 768px) {
      .about-us .about-us__paragraph_second {
        display: block;
        margin-top: 24px; } }
  .about-us .about-us__btn {
    display: block;
    position: relative;
    width: 160px;
    margin-top: 16px;
    padding-left: 24px;
    background-color: #F8F8F8;
    color: black;
    height: 24px;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px; }
    .about-us .about-us__btn::before {
      display: inline-block;
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      left: 0px;
      background: url(./static/blue-arrows.svg) no-repeat; }
  .about-us .about-us__photo {
    max-width: 320px;
    height: 176px;
    margin-top: 16px;
    margin-left: -16px; }
    @media (min-width: 380px) {
      .about-us .about-us__photo {
        margin-left: 0px; } }
    @media (min-width: 768px) {
      .about-us .about-us__photo {
        max-width: 320px;
        height: 208px;
        margin-top: 15px;
        margin-left: 20vw;
        border-radius: 5px; } }
    @media (min-width: 1120px) {
      .about-us .about-us__photo {
        margin: 0;
        margin-left: 36px;
        width: 532px;
        height: 187px; } }
  @media (min-width: 768px) {
    .about-us {
      width: 100%;
      display: flex;
      flex-direction: row; } }

@media (min-width: 768px) {
  .about-us__span {
    display: none; } }

@media (min-width: 1120px) {
  .about-us__paragraph_second .about-us__span {
    display: inline; } }
