.content {
  font-family: TT Lakes, sans-serif;
  background-color: #F8F8F8;
  color: #1B1C21; }
  .content .container {
    display: flex;
    flex-direction: column;
    max-width: 760px; }
    @media (min-width: 768px) {
      .content .container {
        max-width: 1110px; } }
    @media (min-width: 1120px) {
      .content .container {
        width: 1040px; } }
  .content .content__container {
    z-index: 0;
    position: relative;
    padding: 0 16px;
    margin: 0 auto; }
    .content .content__container .site-body {
      display: flex; }
    @media (min-width: 768px) {
      .content .content__container {
        max-width: 1110px;
        margin: 0 auto; } }
