.content {
  font-family: TT Lakes, sans-serif;
  background-color: #F8F8F8;
  color: #1B1C21; }
  .content .container {
    display: flex;
    flex-direction: column;
    max-width: 760px; }
    @media (min-width: 768px) {
      .content .container {
        max-width: 1110px; } }
    @media (min-width: 1120px) {
      .content .container {
        width: 1040px; } }
  .content .content__container {
    z-index: 0;
    position: relative;
    padding: 0 16px;
    margin: 0 auto; }
    .content .content__container .site-body {
      display: flex; }
    @media (min-width: 768px) {
      .content .content__container {
        max-width: 1110px;
        margin: 0 auto; } }

.main-menu_closed {
  display: none; }

.main-menu {
  z-index: 2;
  position: absolute;
  margin-left: -15px;
  width: 320px;
  height: 1024px;
  background: white;
  box-shadow: 2px 2px 50px #8b8b8b, 0px 0px 300px 700px rgba(255, 255, 255, 0.9);
  font-family: TT lakes;
  line-height: 24px;
  color: black; }
  .main-menu .main-menu__container {
